import React from 'react'
import Link from 'gatsby-link'

const AllTags = ({ pathContext }) => {
  const { tags } = pathContext

  if (tags) {
    return (
      <div className='avenir'>
        Stuff I've written about:
        <ul className='avenir moon-gray'>
          {tags.sort().map(tag => {
             return (
              <li className='avenir'>
                <Link className='dark-gray' to={`/tags/${tag}`}>
                  {tag}
                </Link>
              </li>
             )
          })}
        </ul>
      </div>
    )
  }
}

export default AllTags





